import React from 'react';
import { connect } from 'react-redux';
import Profile from './Profile.jsx';
import {getProfileTC, getStatusTC,updateStatusTC} from '../../Redux/profile-reduser';
import { withRouter } from 'react-router-dom';
import { getProfileInfoS, getProfileStatusS, getProfileStatusSuper } from '../../Redux/selectors/profile-selectors.js';

class ProfileAPI extends React.Component{
  
  componentDidMount(){
    
    let userId = this.props.match.params.userId;
    !userId ? userId=19220:userId=userId;
    this.props.getProfileTC(userId);
    this.props.getStatusTC(userId);
    
    
  }
  render(){

    return (
      <Profile {...this.props} />
    )
  }
}

let mapStateToProps = (state) => {
  return {
      profileInfo: getProfileInfoS(state),
      status: getProfileStatusSuper(state),
      
  }
}

let obj = {
  getProfileTC,
  getStatusTC,
  updateStatusTC,
}

const ProfileContainer = connect(mapStateToProps,obj )(withRouter(ProfileAPI));
export default ProfileContainer;
