
import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import s from './ProfileInfo.module.css';

const ProfileInfo = (props) => {

  let [editMode, setEditMode] = useState(false);
  let [status, setStatus] = useState(props.status);

  useEffect(()=>{
    setStatus(props.status);
  },[props.status]);

  const updateEditMode = (e) => {
    setEditMode(!editMode);
    editMode && props.updateStatusTC(e.currentTarget.value)

  }
  const onChangeStatus = (e) => {
    setStatus(e.currentTarget.value);
  }

  return (
    <div>
      <div className={s.mainImg}>
        {//<img src="https://w-dog.ru/wallpapers/0/60/327293351030583/ist-river-most-nyu-jork-nochnoj-gorod-proliv.jpg"  alt="" />
        }
      </div>
      <div className={s.avaInfo}>
        <div className={s.avatar}>
          <img src={props.profileInfo.photos.large ? props.profileInfo.photos.large : "https://pm1.narvii.com/7924/1da85cd6f60fceda99aa450734fb8a3596731e4er1-690-690v2_hq.jpg"} alt="" />

        </div>
        <div className={s.info}>
          <h1>{props.profileInfo.fullName}</h1>
          <p>{props.profileInfo.aboutMe}</p>
          <div>
            {
              editMode
                ? <input onChange={onChangeStatus} onBlur={updateEditMode} autoFocus={true} value={status} />
                : <p onClick={updateEditMode}>Status: {props.status}</p>
            }


          </div>



        </div>
      </div>
    </div>

  );
}



export default ProfileInfo;